import React from "react";
import {Helmet} from 'react-helmet';
import { BallBeat } from 'react-pure-loaders';
import {useQuery, gql} from '@apollo/client';
import { Link } from "react-router-dom";
const BLOGS_QUERRY = gql`
    {
        posts(start:0,limit:10,where:{published: true}, sort: "updated_at:ASC") {
            name
            description
            updated_at
            slug
            thumbnail {
                url
            }
            categories {
                name
            }
        } 
    }
`
const BlogList = () => {
    const { loading, error, data } = useQuery(BLOGS_QUERRY)
    if (loading) return <BallBeat color={'#D465EF'}loading={loading}/>
    if (error) return <p>Error :( {JSON.stringify(error)}</p>;
    const {posts} = data ;
    return(
        <>
        <Helmet>
                <title>My Blogs</title>
                <meta name="description" content="Phat's Blogs" />
        </Helmet>
        <div className="col-lg-8 mb-5 mb-lg-0">
                            <div className="blog_left_sidebar">
                                {
                                    posts.map((post, index) => {
                                        const date = new Date(post.updated_at).toDateString().split(' ')
                                        return (
                                            <article className="blog_item">
                                            <div className="blog_item_img">
                                                <img className="card-img rounded-0 blog-image-thumbnail" src={`https://arcane-shore-71313.herokuapp.com/${post.thumbnail.url}`} alt=""/>
                                                    <a href="#" className="blog_item_date">
                                                        <h3>{date[2]}</h3>
                                                        <p>{date[1]}</p>
                                                    </a>
                                            </div>
        
                                            <div className="blog_details">
                                                <Link className="d-inline-block" to={`/blogs/${post.slug}`}>
                                                    <h2>{post.name}</h2>
                                                </Link>
                                                <p>{post.description}</p>
                                                <ul className="blog-info-link">
                                                <li>
                                                {post.categories.map((category, index) => {
                                                    return <a href="#">{`${category.name}${post.categories[index+1]?', ': ''}`}</a>
                                                })}
                                                    </li>
                                                    <li><a href="#"><i className="fa fa-comments"></i> 03 Comments</a></li>
                                                </ul>
                                            </div>
                                        </article>
        
                                        )
                                    })
                                }



                                <nav className="blog-pagination justify-content-center d-flex">
                                    <ul className="pagination">
                                        <li className="page-item">
                                            <a href="#" className="page-link" aria-label="Previous">
                                                <i className="ti-angle-left"></i>
                                            </a>
                                        </li>
                                        <li className="page-item">
                                            <a href="#" className="page-link">1</a>
                                        </li>
                                        <li className="page-item active">
                                            <a href="#" className="page-link">2</a>
                                        </li>
                                        <li className="page-item">
                                            <a href="#" className="page-link" aria-label="Next">
                                                <i className="ti-angle-right"></i>
                                            </a>
                                        </li>
                                    </ul>
                                </nav>
                            </div>
                        </div>
        </>
    )   
}
export default BlogList