import React from 'react';
import {useQuery, gql} from '@apollo/client';
import { BallBeat } from 'react-pure-loaders';
import {Link} from 'react-router-dom';
const PORTFOLIO_QUERY = gql`
    {
        portfolios {
            name,
            thumbnail {
                url
            }
        }
    }
`;
const PortfolioShow = () => {
    const { loading, error, data } = useQuery(PORTFOLIO_QUERY);
    if (loading) return <BallBeat color={'#D465EF'}loading={loading}/>
    if (error) return <p>Error :( </p>;
    const {portfolios} = data
    return (
        <div className="portfolio_image_area">
                <div className="container">
                    <div className="row">
                        <div className="col-xl-5 col-md-5">
                            <div className="single_Portfolio">
                                <div className="portfolio_thumb">
                                    <img src={`https://arcane-shore-71313.herokuapp.com/${portfolios[0].thumbnail.url}`} alt=""/>
                                </div>
                                <a href={`https://arcane-shore-71313.herokuapp.com/${portfolios[0].thumbnail.url}`} className="popup popup-image"></a>
                                <div className="portfolio_hover">
                                    <div className="title">
                                    <h3>{portfolios[0].name}</h3>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="col-xl-7 col-md-7">
                            <div className="single_Portfolio">
                                <div className="portfolio_thumb">
                                    <img src={`https://arcane-shore-71313.herokuapp.com/${portfolios[1].thumbnail.url}`} alt=""/>
                                </div>
                                <a href={`https://arcane-shore-71313.herokuapp.com/${portfolios[1].thumbnail.url}`} className="popup popup-image"></a>
                                <div className="portfolio_hover">
                                    <div className="title">
                                        <h3>{portfolios[1].name}</h3>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="col-xl-4 col-md-6 col-lg-4">
                            <div className="single_Portfolio">
                                <div className="portfolio_thumb">
                                    <img src={`https://arcane-shore-71313.herokuapp.com/${portfolios[2].thumbnail.url}`} alt=""/>
                                </div>
                                <a href={`https://arcane-shore-71313.herokuapp.com/${portfolios[2].thumbnail.url}`} className="popup popup-image"></a>
                                <div className="portfolio_hover">
                                    <div className="title">
                                        <h3>{portfolios[2].name}</h3>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="col-xl-4 col-md-6 col-lg-4">
                            <div className="single_Portfolio">
                                <div className="portfolio_thumb">
                                    <img src={`https://arcane-shore-71313.herokuapp.com/${portfolios[3].thumbnail.url}`} alt=""/>
                                </div>
                                <a href={`https://arcane-shore-71313.herokuapp.com/${portfolios[3].thumbnail.url}`} className="popup popup-image"></a>
                                <div className="portfolio_hover">
                                    <div className="title">
                                        <h3>{portfolios[3].name}</h3>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="col-xl-4 col-md-12 col-lg-4">
                            <div className="single_Portfolio">
                                <div className="portfolio_thumb">
                                    <img src={`https://arcane-shore-71313.herokuapp.com/${portfolios[4].thumbnail.url}`} alt=""/>
                                </div>
                                <a href={`https://arcane-shore-71313.herokuapp.com/${portfolios[4].thumbnail.url}`} className="popup popup-image"></a>
                                <div className="portfolio_hover">
                                    <div className="title">
                                        <h3>{portfolios[4].name}</h3>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-xl-12">
                            <div className="more_portfolio text-center">
                                <Link className="line_btn" to="/portfolio"> More Folio</Link>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
    )
}
export default PortfolioShow