import React from "react";
import Bradcam from "../../components/Bradcam";
import BlogCategories from "../../components/BlogCategories";
import RecentPosts from "../../components/RecentPosts";
import { Switch, Route } from "react-router-dom";
import BlogList from "../../components/BlogList";
import BlogDetail from "../../components/BlogDetail";
const BlogPage = () => {   
    return (
        <>
            <Bradcam title={'Blogs'}/>
            <section className="blog_area section-padding">
                <div className="container">
                    <div className="row">
                    <Switch>
                        <Route exact path={'/blogs'}>
                            <BlogList/>
                        </Route>
                        <Route path={'/blogs/:slug'}>
                            <BlogDetail/>
                        </Route>
                    </Switch>
                        <div className="col-lg-4">
                            <div className="blog_right_sidebar">
                                <aside className="single_sidebar_widget search_widget">
                                    <form action="#">
                                        <div className="form-group">
                                            <div className="input-group mb-3">
                                                <input type="text" className="form-control" placeholder='Search Keyword'
                                                       onFocus="this.placeholder = ''"
                                                       onBlur="this.placeholder = 'Search Keyword'"/>
                                                    <div className="input-group-append">
                                                        <button className="btn" type="button"><i
                                                            className="ti-search"></i></button>
                                                    </div>
                                            </div>
                                        </div>
                                        <button className="button rounded-0 primary-bg text-white w-100 btn_1 boxed-btn"
                                                type="submit">Search
                                        </button>
                                    </form>
                                </aside>

                                <BlogCategories/>

                                <RecentPosts/>

                                <aside className="single_sidebar_widget newsletter_widget">
                                    <h4 className="widget_title">Newsletter</h4>

                                    <form action="#">
                                        <div className="form-group">
                                            <input type="email" className="form-control" onFocus="this.placeholder = ''"
                                                   onBlur="this.placeholder = 'Enter email'" placeholder='Enter email'
                                                   required/>
                                        </div>
                                        <button className="button rounded-0 primary-bg text-white w-100 btn_1 boxed-btn"
                                                type="submit">Subscribe
                                        </button>
                                    </form>
                                </aside>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </>
    )
};
export default BlogPage
