import React from "react";
import {Helmet} from 'react-helmet';
import { BallBeat } from 'react-pure-loaders';
import {useQuery, gql} from '@apollo/client';
import { useParams } from "react-router-dom";
import ReactMarkdown from 'react-markdown';

const BlogDetail = () => {
    const {slug} = useParams();
    const BLOG_DATA = gql` {
        posts(where: {slug: "${slug}"}) {
            name
            content
            thumbnail {
                url
            }
            categories {
                name
            }
        }
    }
    
`
    const { loading, error, data } = useQuery(BLOG_DATA)
    if (loading) return <BallBeat color={'#D465EF'}loading={loading}/>
    if (error) return <p>Error :( {JSON.stringify(error)}</p>;
    console.log(data)
    const post = data.posts[0] ;
    return (
        <>
            <Helmet>
                <title>{post.name}</title>
                <meta name="description" content={post.name} />
            </Helmet>
            <div className="col-lg-8 posts-list">
                            <div className="single-post">
                                <div className="feature-img">
                                    <img className="img-fluid blog-image-thumbnail" src={`https://arcane-shore-71313.herokuapp.com/${post.thumbnail.url}`} alt=""/>
                                </div>
                                <div className="blog_details">
                                    <h2>{post.name}
                                    </h2>
                                    <ul className="blog-info-link mt-3 mb-4">
                                        
                                        <li><i className="fa fa-user"></i>{post.categories.map((category, index) => {
                                                    return <a href="#">{`${category.name}${post.categories[index+1]?', ': ''}`}</a>
                                                })}</li>
                                    </ul>
                                    <div>
                                        <ReactMarkdown source={post.content} />
                                    </div>
                                   
                                </div>
                            </div>
                            <div className="navigation-top">
                                <div className="d-sm-flex justify-content-between text-center">
                                    <p className="like-info"><span className="align-middle"><i
                                        className="fa fa-heart"></i></span> Lily and 4
                                        people like this</p>
                                    <div className="col-sm-4 text-center my-2 my-sm-0">

                                    </div>
                                    <ul className="social-icons">
                                        <li><a href="#"><i className="fa fa-facebook-f"></i></a></li>
                                        <li><a href="#"><i className="fa fa-twitter"></i></a></li>
                                        <li><a href="#"><i className="fa fa-dribbble"></i></a></li>
                                        <li><a href="#"><i className="fa fa-behance"></i></a></li>
                                    </ul>
                                </div>
                                <div className="navigation-area">
                                    <div className="row">
                                        <div
                                            className="col-lg-6 col-md-6 col-12 nav-left flex-row d-flex justify-content-start align-items-center">
                                            <div className="thumb">
                                                <a href="#">
                                                    <img className="img-fluid" src="img/post/preview.png" alt=""/>
                                                </a>
                                            </div>
                                            <div className="arrow">
                                                <a href="#">
                                                    <span className="lnr text-white ti-arrow-left"></span>
                                                </a>
                                            </div>
                                            <div className="detials">
                                                <p>Prev Post</p>
                                                <a href="#">
                                                    <h4>Space The Final Frontier</h4>
                                                </a>
                                            </div>
                                        </div>
                                        <div
                                            className="col-lg-6 col-md-6 col-12 nav-right flex-row d-flex justify-content-end align-items-center">
                                            <div className="detials">
                                                <p>Next Post</p>
                                                <a href="#">
                                                    <h4>Telescopes 101</h4>
                                                </a>
                                            </div>
                                            <div className="arrow">
                                                <a href="#">
                                                    <span className="lnr text-white ti-arrow-right"></span>
                                                </a>
                                            </div>
                                            <div className="thumb">
                                                <a href="#">
                                                    <img className="img-fluid" src="img/post/next.png" alt=""/>
                                                </a>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="blog-author">
                                <div className="media align-items-center">
                                    <img src="img/blog/author.png" alt=""/>
                                        <div className="media-body">
                                            <a href="#">
                                                <h4>Harvard milan</h4>
                                            </a>
                                            <p>Second divided from form fish beast made. Every of seas all gathered use
                                                saying you're, he
                                                our dominion twon Second divided from</p>
                                        </div>
                                </div>
                            </div>
                            <div className="comments-area">
                                <h4>05 Comments</h4>
                                <div className="comment-list">
                                    <div className="single-comment justify-content-between d-flex">
                                        <div className="user justify-content-between d-flex">
                                            <div className="thumb">
                                                <img src="img/comment/comment_1.png" alt=""/>
                                            </div>
                                            <div className="desc">
                                                <p className="comment">
                                                    Multiply sea night grass fourth day sea lesser rule open subdue
                                                    female fill which them
                                                    Blessed, give fill lesser bearing multiply sea night grass fourth
                                                    day sea lesser
                                                </p>
                                                <div className="d-flex justify-content-between">
                                                    <div className="d-flex align-items-center">
                                                        <h5>
                                                            <a href="#">Emilly Blunt</a>
                                                        </h5>
                                                        <p className="date">December 4, 2017 at 3:12 pm </p>
                                                    </div>
                                                    <div className="reply-btn">
                                                        <a href="#" className="btn-reply text-uppercase">reply</a>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="comment-list">
                                    <div className="single-comment justify-content-between d-flex">
                                        <div className="user justify-content-between d-flex">
                                            <div className="thumb">
                                                <img src="img/comment/comment_2.png" alt=""/>
                                            </div>
                                            <div className="desc">
                                                <p className="comment">
                                                    Multiply sea night grass fourth day sea lesser rule open subdue
                                                    female fill which them
                                                    Blessed, give fill lesser bearing multiply sea night grass fourth
                                                    day sea lesser
                                                </p>
                                                <div className="d-flex justify-content-between">
                                                    <div className="d-flex align-items-center">
                                                        <h5>
                                                            <a href="#">Emilly Blunt</a>
                                                        </h5>
                                                        <p className="date">December 4, 2017 at 3:12 pm </p>
                                                    </div>
                                                    <div className="reply-btn">
                                                        <a href="#" className="btn-reply text-uppercase">reply</a>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="comment-list">
                                    <div className="single-comment justify-content-between d-flex">
                                        <div className="user justify-content-between d-flex">
                                            <div className="thumb">
                                                <img src="img/comment/comment_3.png" alt=""/>
                                            </div>
                                            <div className="desc">
                                                <p className="comment">
                                                    Multiply sea night grass fourth day sea lesser rule open subdue
                                                    female fill which them
                                                    Blessed, give fill lesser bearing multiply sea night grass fourth
                                                    day sea lesser
                                                </p>
                                                <div className="d-flex justify-content-between">
                                                    <div className="d-flex align-items-center">
                                                        <h5>
                                                            <a href="#">Emilly Blunt</a>
                                                        </h5>
                                                        <p className="date">December 4, 2017 at 3:12 pm </p>
                                                    </div>
                                                    <div className="reply-btn">
                                                        <a href="#" className="btn-reply text-uppercase">reply</a>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="comment-form">
                                <h4>Leave a Reply</h4>
                                <form className="form-contact comment_form" action="#" id="commentForm">
                                    <div className="row">
                                        <div className="col-12">
                                            <div className="form-group">
                              <textarea className="form-control w-100" name="comment" id="comment" cols="30" rows="9"
                                        placeholder="Write Comment"></textarea>
                                            </div>
                                        </div>
                                        <div className="col-sm-6">
                                            <div className="form-group">
                                                <input className="form-control" name="name" id="name" type="text"
                                                       placeholder="Name"/>
                                            </div>
                                        </div>
                                        <div className="col-sm-6">
                                            <div className="form-group">
                                                <input className="form-control" name="email" id="email" type="email"
                                                       placeholder="Email"/>
                                            </div>
                                        </div>
                                        <div className="col-12">
                                            <div className="form-group">
                                                <input className="form-control" name="website" id="website" type="text"
                                                       placeholder="Website"/>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="form-group">
                                        <button type="submit" className="button button-contactForm btn_1 boxed-btn">Send
                                            Message
                                        </button>
                                    </div>
                                </form>
                            </div>
                        </div>
        </>
    )

}
export default BlogDetail