import React from 'react';
import { BallBeat } from 'react-pure-loaders';
import {useQuery, gql} from '@apollo/client';
const POSTS_QUERY = gql`
    {
        posts(limit:4,where:{published: true}, sort: "updated_at:ASC") {
            name
            updated_at
            thumbnail {
                url
            }
        }   
    }
`
const RecentPosts = () => {
    const { loading, error, data } = useQuery(POSTS_QUERY)
    if (loading) return <BallBeat color={'#D465EF'}loading={loading}/>
    if (error) return <p>Error :( {JSON.stringify(error)}</p>;
    const {posts} = data;
    return (
        <aside className="single_sidebar_widget popular_post_widget">
        <h3 className="widget_title">Recent Post</h3>
        {
            posts.map((post, index) => (
                <div className="media post_item" key={index}>
                <img className="blog-small-image-thumbnail" src={`https://arcane-shore-71313.herokuapp.com/${post.thumbnail.url}`} alt="post"/>
                    <div className="media-body">
                        <a href="single-blog.html">
                        <h3>{post.name}</h3>
                        </a>
                        <p>{new Date(post.updated_at).toDateString()}</p>
                    </div>
            </div>
            ))
        }
    </aside>
    )
}
export default RecentPosts;