import React from "react";
import { Link } from "react-router-dom";
const ProjectDiscuss = () => {
    return (
        <div className="discuss_projects">
            <div className="container">
                <div className="row">
                    <div className="col-xl-12">
                        <div className="project_text text-center">
                            <h3>Have an idea in mind ?</h3>
                            <p>If you have an idea to establish a business,
                                <br/> do not hesitate to talk to me and make it true.</p>
                            <Link className="boxed-btn3" to="/contact">Start Talking</Link>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
};
export default ProjectDiscuss
