import React from "react";
import {Link} from 'react-router-dom'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { BallBeat } from 'react-pure-loaders';
import {faFacebookF, faInstagram, faLinkedinIn} from '@fortawesome/free-brands-svg-icons'
import {useQuery, gql} from '@apollo/client';
const SETTING_DATA = gql`
    {
        setting {
            socialLinks
        }
    }
`
const Footer = () => {
    const { loading, error, data } = useQuery(SETTING_DATA)
    if (loading) return <BallBeat color={'#D465EF'}loading={loading}/>
    if (error) return <p>Error :(</p>;
    const {socialLinks} = data.setting
    return (
        <footer className="footer">
            <div className="footer_top">
                <div className="container">
                    <div className="row align-items-center">
                        <div className="col-xl-6 col-md-6">
                            <div className="menu_links">
                                <ul>
                                    <li><Link to='about'>About</Link></li>
                                    <li><Link to='services'>Services</Link></li>
                                    <li><Link to='portfolio'>Portfolio</Link></li>
                                </ul>
                            </div>
                        </div>
                        <div className="col-xl-6 col-md-6">
                            <div className="socail_links">
                                <ul>
                                    <li><a href={socialLinks.facebook}> <FontAwesomeIcon icon={faFacebookF} size="x"/> </a></li>
                                    <li><a href={socialLinks.instagram}> <FontAwesomeIcon icon={faInstagram} size="x"/> </a></li>
                                    <li><a href={socialLinks.linkedin}> <FontAwesomeIcon icon={faLinkedinIn} size="x"/> </a></li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="copy-right_text">
                <div className="container">
                    <div className="footer_border"></div>
                    <div className="row">
                        <div className="col-xl-12">
                            <p className="copy_right text-center">

                                Copyright &copy;
                                {`${new Date().getFullYear()} `}
                                All rights reserved | This template is made with <i className="fa fa-heart-o"
                                                                                    aria-hidden="true"></i> by <a
                                href="https://colorlib.com" target="_blank" rel="noopener noreferrer">Colorlib</a>

                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </footer>
    )
};
export default Footer
